export interface DrawerHelperOptions {
  /**
   * Ã¥Â¤Â§Ã¥Â°ÂÃ¯Â¼ÂÃ¨ÂÂ¥Ã¥ÂÂ¼Ã¤Â¸ÂºÃ¦ÂÂ°Ã¥ÂÂ¼Ã§Â±Â»Ã¥ÂÂÃ¯Â¼ÂÃ¥ÂÂÃ¦Â Â¹Ã¦ÂÂ® `nzPlacement` Ã¨ÂÂªÃ¥ÂÂ¨Ã¨Â½Â¬Ã¥ÂÂÃ¤Â¸Âº `nzHeight` Ã¦ÂÂ `nzWidth`Ã¯Â¼ÂÃ¤Â¾ÂÃ¥Â¦ÂÃ¯Â¼ÂlgÃ£ÂÂ600Ã¯Â¼ÂÃ©Â»ÂÃ¨Â®Â¤Ã¯Â¼Â`md`
   *
   * | Ã§Â±Â»Ã¥ÂÂ | Ã©Â»ÂÃ¨Â®Â¤Ã¥Â¤Â§Ã¥Â°Â |
   * | --- | ------ |
   * | `sm` | `300` |
   * | `md` | `600` |
   * | `lg` | `900` |
   * | `xl` | `1200` |
   *
   * > Ã¤Â»Â¥Ã¤Â¸ÂÃ¥ÂÂ¼Ã¯Â¼ÂÃ¥ÂÂ¯Ã©ÂÂÃ¨Â¿ÂÃ¨Â¦ÂÃ§ÂÂÃ§ÂÂ¸Ã¥ÂºÂÃ§ÂÂLESSÃ¥ÂÂÃ¦ÂÂ°Ã¨ÂÂªÃ¨Â¡ÂÃ¨Â°ÂÃ¦ÂÂ´
   */
  size?: 'sm' | 'md' | 'lg' | 'xl' | number;
  /**
   * Ã¦ÂÂ¯Ã¥ÂÂ¦Ã¥ÂÂÃ¥ÂÂ«Ã¥ÂºÂÃ©ÂÂ¨Ã¥Â·Â¥Ã¥ÂÂ·Ã¦ÂÂ¡Ã¯Â¼ÂÃ©Â»ÂÃ¨Â®Â¤Ã¯Â¼Â`true`
   */
  footer?: boolean;
  /**
   * Ã¥ÂºÂÃ©ÂÂ¨Ã¥Â·Â¥Ã¥ÂÂ·Ã¦ÂÂ¡Ã©Â«ÂÃ¥ÂºÂ¦Ã¯Â¼ÂÃ©Â»ÂÃ¨Â®Â¤Ã¯Â¼Â`55`
   */
  footerHeight?: number;
  /** Ã¦ÂÂ½Ã¥Â±Â [NzDrawerOptions](https://ng.ant.design/components/drawer/zh#nzdraweroptions) Ã¥ÂÂÃ¦ÂÂ° */
  drawerOptions?: NzDrawerOptions;
}

/**
 * Ã¦ÂÂ½Ã¥Â±ÂÃ¨Â¾ÂÃ¥ÂÂ©Ã§Â±Â»
 *
 * **Ã¦Â³Â¨Ã¦ÂÂÃ¯Â¼Â** Ã¦ÂÂÃ¥Â»ÂºÃ§Â»ÂÃ¦ÂÂÃ©ÂÂ½Ã¥ÂÂ¯Ã¨Â¢Â«Ã¨Â®Â¢Ã©ÂÂÃ¯Â¼ÂÃ¤Â½ÂÃ¦Â°Â¸Ã¨Â¿ÂÃ©ÂÂ½Ã¤Â¸ÂÃ¤Â¼ÂÃ¨Â§Â¦Ã¥ÂÂ `observer.error`
 *
 * Ã§Â¤ÂºÃ¤Â¾ÂÃ¯Â¼Â
```ts
this.drawerHelper.create('Edit', FormEditComponent, { i }).subscribe(res => this.load());
// Ã¥Â¯Â¹Ã¤ÂºÂÃ§Â»ÂÃ¤Â»Â¶Ã§ÂÂÃ¦ÂÂÃ¥ÂÂ&Ã¥ÂÂ³Ã©ÂÂ­Ã§ÂÂÃ¥Â¤ÂÃ§ÂÂÃ¨Â¯Â´Ã¦ÂÂ
// Ã¦ÂÂÃ¥ÂÂ
this.NzDrawerRef.close(data);
this.NzDrawerRef.close(true);
// Ã¥ÂÂ³Ã©ÂÂ­
this.NzDrawerRef.close();
this.NzDrawerRef.close(false);
```
 */
@Injectable({ providedIn: 'root' })
export class DrawerHelper {
  // Ã¥Â¤Â§Ã©ÂÂ¨Ã¥ÂÂÃ¦ÂÂÃ¥ÂÂµÃ¤Â¸ÂÃ¦ÂÂ½Ã¥Â±ÂÃ§ÂÂÃ¥Â±ÂÃ§ÂºÂ§Ã¦Â¯Â Modal Ã¤Â¼ÂÃ¦ÂÂ´Ã¤Â½ÂÃ¤Â¸ÂÃ¤ÂºÂ
  private zIndex = 400;

  constructor(private srv: NzDrawerService) {}

  /**
   * Ã¦ÂÂÃ¥Â»ÂºÃ¤Â¸ÂÃ¤Â¸ÂªÃ¦ÂÂ½Ã¥Â±Â
   */
  create(title: string, comp: any, params?: any, options?: DrawerHelperOptions): Observable<any> {
    options = {
      size: 'md',
      footer: true,
      footerHeight: 55,
      drawerOptions: {
        nzPlacement: 'right',
        nzWrapClassName: '',
      },
      ...options,
    };
    return new Observable((observer: Observer<any>) => {
      const { size, footer, footerHeight, drawerOptions } = options;
      const defaultOptions: NzDrawerOptions = {
        nzContent: comp,
        nzContentParams: params,
        nzZIndex: ++this.zIndex,
        nzTitle: title,
      };

      if (typeof size === 'number') {
        defaultOptions[
          drawerOptions.nzPlacement === 'top' || drawerOptions.nzPlacement === 'bottom'
            ? 'nzHeight'
            : 'nzWidth'
        ] = options.size;
      } else {
        defaultOptions.nzWrapClassName = (
          drawerOptions.nzWrapClassName + ` drawer-${options.size}`
        ).trim();
        delete drawerOptions.nzWrapClassName;
      }

      if (footer) {
        const { nzPlacement, nzHeight } = drawerOptions;
        // Should be header * footer, because of includes header
        const reduceHeight = (footerHeight * 2) - 2;
        if (nzPlacement === 'left' || nzPlacement === 'right') {
          defaultOptions.nzBodyStyle = {
            height: `calc(100% - ${reduceHeight}px)`,
            overflow: 'auto',
          };
        } else {
          defaultOptions.nzBodyStyle = {
            height: `${+(nzHeight || 256) - reduceHeight}px`,
            overflow: 'auto',
          };
        }
      }

      const subject = this.srv.create({ ...defaultOptions, ...drawerOptions });
      const afterClose$ = subject.afterClose.subscribe((res: any) => {
        if (res != null && res !== false) {
          observer.next(res);
        }
        observer.complete();
        afterClose$.unsubscribe();
      });
    });
  }

  /**
   * Ã¦ÂÂÃ¥Â»ÂºÃ¤Â¸ÂÃ¤Â¸ÂªÃ¦ÂÂ½Ã¥Â±ÂÃ¯Â¼ÂÃ§ÂÂ¹Ã¥ÂÂ»Ã¨ÂÂÃ¥Â±ÂÃ¤Â¸ÂÃ¥ÂÂÃ¨Â®Â¸Ã¥ÂÂ³Ã©ÂÂ­
   */
  static(title: string, comp: any, params?: any, options?: DrawerHelperOptions): Observable<any> {
    const drawerOptions = {
      nzMaskClosable: false,
      ...(options && options.drawerOptions),
    };
    return this.create(title, comp, params, { ...options, drawerOptions });
  }
}
